/**
* 
* {{ Date() | formatDate }} -> 2020-09-28 15:54:52
* {{ '2020/11/06 12:30:45' | formatDate('yyyy-MM-dd hh:mm:ss w') }} -> 2020-11-06 12:30:45 
* @param {Date} value  new Date(value)  Date()2020/11/062020-11-06 12:00 
* @param {String} fmt 
*/
export const formatDate = (() => {
    const weekMap = new Map([
        [0, ''],
        [1, ''],
        [2, ''],
        [3, ''],
        [4, ''],
        [5, ''],
        [6, '']
    ]);
    return function formatDate(value, fmt = 'yyyy-MM-dd hh:mm:ss') {
        if (value === '' || value === undefined || value === null)
            return '';
        const date = new Date(value);
        const normalData = {
            // 
            'M+': date.getMonth() + 1,
            // 
            'd+': date.getDate(),
            // 
            'h+': date.getHours(),
            // 
            'm+': date.getMinutes(),
            // 
            's+': date.getSeconds(),
            // 
            'w+': date.getDay(),
            // 
            'q+': Math.floor((date.getMonth() + 3) / 3),
            // 
            S: date.getMilliseconds()
        };
        if (/(y+)/.test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
        }
        for (const key in normalData) {
            if (key === 'w+') {
                fmt = fmt.replace('w', weekMap.get(normalData[key]));
            }
            else if (new RegExp(`(${key})`).test(fmt)) {
                fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1
                    ? normalData[key]
                    : ('00' + normalData[key]).substr(('' + normalData[key]).length));
            }
        }
        return fmt;
    };
})();
